/* ELEMENT STYLES -------------------- */
body {
  background: #e8e7e7;
  font-family: arial;
  text-transform: uppercase;
  font-family: "Play", sans-serif;
}

header {
  color: #fff;
  padding: 5px 10px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #ff9800;
  border-bottom: solid 2px #d4caff;
}

header h1 {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  font-size: 2.5em;
  letter-spacing: 2px;
  font-weight: normal;
  font-family: "Russo One", sans-serif;
}

/* FORMS */
form {
  display: -webkit-flex;
  display: flex;
  background-color: #ff9800;
}

input[type="text"] {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  border-width: 0 0 1px 0;
  margin: 15px 10px 15px 15px;
  padding: 10px;
  border-radius: 5px;
  background-color: #ff5722;
  border-style: none;
  text-shadow: none;
  text-transform: uppercase;
  color: #ffeb3b;
  letter-spacing: 2px;
  outline: none;
}

input[type="text"]::-webkit-input-placeholder {
  color: #dcdcdc;
  letter-spacing: 2px;
}

input[type="text"]:focus {
  background-color: rgba(0, 0, 0, 0.2);
}

input[type="submit"] {
  display: block;
  font-size: 0.6em;
  margin: 15px 15px 15px 0;
  padding: 10px;
  background-color: #ff5722;
  border-radius: 5px;
  border: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: bold;
  text-shadow: none;
  text-transform: uppercase;
  cursor: pointer;
}

button,
input[type="submit"] {
  outline: none;
  transition: background-color 0.2s ease, color 0.2s ease;
}

/* ICONS */
svg {
  width: 28px;
  height: 19px;
  margin-right: 5px;
  fill: #f0f0f0;
}

.is-high-score {
  fill: #ffc657;
  -webkit-animation: grow 0.35s ease-in-out;
          animation: grow 0.35s ease-in-out;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

@-webkit-keyframes grow {
  50% {
    -webkit-transform: scale(1.4) rotate(-3deg);
            transform: scale(1.4) rotate(-3deg);
  }
}

@keyframes grow {
  50% {
    -webkit-transform: scale(1.4) rotate(-3deg);
            transform: scale(1.4) rotate(-3deg);
  }
}

/* COMPONENT STYLES -------------------- */
.scoreboard {
  background: #fff;
  width: 700px;
  margin: 70px auto;
  border-radius: 6px;
  overflow: hidden;
}

.player {
  display: -webkit-flex;
  display: flex;
  font-size: 1.2em;
  border-bottom: solid 2px #eeeeee;
  letter-spacing: 2px;
}

.player:last-of-type {
  border-bottom-color: #d4caff;
}

.player-name:hover .remove-player {
  visibility: visible;
}

.player-name,
.counter-score {
  color: #2f2366;
}

.player-name {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  line-height: 3.5em;
  padding-left: 10px;
}

.remove-player {
  font-size: 1.15em;
  border: none;
  outline: none;
  visibility: hidden;
  color: #ff5722;
  cursor: pointer;
  margin: 0 10px 0 0;
  padding: 0;
  background: none;
}

.stats,
.stopwatch {
  width: 170px;
}

.stats {
  display: -webkit-flex;
  display: flex;
  margin: 0 0 0 28px;
  font-weight: normal;
}

.stats td:first-child {
  text-align: right;
  font-weight: normal;
  letter-spacing: 2px;
  font-size: 0.7em;
}

.stats td:last-child {
  text-align: left;
}

.stopwatch {
  padding: 15px 10px 5px 10px;
  margin: -5px -10px -5px 10px;
  /* background: #5b45b9; */
  background: #ff9800;
  /* border: solid 2px #ffeb3b; */
  /* border-radius: 3%; */
}

.stopwatch-time {
  display: block;
  font-family: monospace;
  font-size: 2.15em;
}

.stopwatch button {
  margin: 8px 5px;
  background-color: #ff9800;
  border-radius: 5px;
  padding: 7px 8px;
  border: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: bold;
  text-shadow: none;
  text-transform: uppercase;
}

.stopwatch button:hover,
input[type="submit"]:hover {
  background: #fff;
  color: #44338d;
  cursor: pointer;
}

.stopwatch h2 {
  font-size: 0.6em;
  margin: 0;
  font-weight: normal;
  letter-spacing: 2px;
}

.counter {
  display: -webkit-flex;
  display: flex;
  width: 190px;
}

.counter-action {
  border: none;
  font-size: 1.15em;
  font-weight: bold;
  color: #b0aeba;
  background-color: #dcdcdc;
  display: block;
  padding: 20px 20px;
  cursor: pointer;
  outline: none;
}

.counter-action:hover {
  color: #fff;
}

.increment:hover {
  background: #83d97c;
}

.decrement:hover {
  background: #ef5350;
}

.counter-score {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: center;
  font-family: monospace;
  font-size: 2em;
  line-height: 2em;
}

